.page{
    width: 100%;
    height: 100vh;
    background-color: #ffffff;
}

.main{
    width: 100%;
    padding-top: 4rem;
    font-size: 0.6rem;
}

.main .cellphone-wrap{
    width: 90%;
    height: 1.8rem;
    margin: 0 auto;
    display: flex;
    display: -webkit-flex;
    align-items: center;
    -webkit-align-items: center;
    margin-bottom: 1rem;
}

.main .cellphone-wrap .cellphone{
    width: 60%;
    height: 100%;
    border: 2px solid #efefef;
    border-radius: 0.2rem;
    -webkit-border-radius: 0.2rem;
    -moz-border-radius: 0.2rem;
    -ms-border-radius: 0.2rem;
    -o-border-radius: 0.2rem;
}

.main .cellphone-wrap .cellphone input{
    width: 90%;
    height: 100%;
    margin-left: 0.5rem;
}

.main .cellphone-wrap .code-btn{
    width: 35%;
    height: 100%;
    margin-left: 0.2rem;
    background-color: #eaeaea;
    border: 2px solid #eaeaea;
    border-radius: 0.2rem;
    -webkit-border-radius: 0.2rem;
    -moz-border-radius: 0.2rem;
    -ms-border-radius: 0.2rem;
    -o-border-radius: 0.2rem;
    text-align: center;
    line-height: 1.8rem;

    color:#918f8f
}

.main .cellphone-wrap .code-btn.success{
    background-color: #ffffff;
    border: 1px solid #eb1625;
    color:#eb1625
}

.main .code-wrap{
    width: 85%;
    height: 1.8rem;
    margin-left: 0.9rem;
    padding-left: 0.5rem;
    border: 2px solid #efefef;
    border-radius: 0.2rem;
    -webkit-border-radius: 0.2rem;
    -moz-border-radius: 0.2rem;
    -ms-border-radius: 0.2rem;
    -o-border-radius: 0.2rem;
    margin-bottom: 1rem;
}

.main .code-wrap input{
    width: 95%;
    height: 100%;
}

.main .password-wrap{
    width: 85%;
    height: 1.8rem;
    margin-left: 0.9rem;
    padding-left: 0.5rem;
    border: 2px solid #efefef;
    border-radius: 0.2rem;
    -webkit-border-radius: 0.2rem;
    -moz-border-radius: 0.2rem;
    -ms-border-radius: 0.2rem;
    -o-border-radius: 0.2rem;
    margin-bottom: 1rem;
    display: flex;
    display: -webkit-flex;
    align-items: center;
    -webkit-align-items: center;
}

.main .password-wrap .password{
    width: 75%;
    height: 100%;
}

.main .password-wrap .password form{
    width: 100%;
    height: 100%;
}

.main .password-wrap .password input{
    width: 95%;
    height: 100%;
}

.main .password-wrap .switch-wrap{
    width: 15%;
    height: 80%;   
    padding-left: 0.8rem;
}

.main .sure-btn{
    width: 85%;
    height: 1.8rem;
    font-size: 0.9rem;
    color: #ffffff;
    letter-spacing: 0.4rem;
    text-align: center;
    line-height: 1.8rem;
    background-color: #eb1625;
    margin-left: 0.9rem;
    padding-left: 0.5rem;
    border: 2px solid #eb1625;
    border-radius: 0.2rem;
    -webkit-border-radius: 0.2rem;
    -moz-border-radius: 0.2rem;
    -ms-border-radius: 0.2rem;
    -o-border-radius: 0.2rem;
    margin-bottom: 0.8rem;
}